	module.exports = {"data":{"wordpressPost":{"title":"Government surveillance systems, cleaning agents and autism: Most read articles in October","content":"<p>The most-read article in October was  Werb <em>et al</em>&#8216;s article on international government surveillance systems. Carpenter <em>et al&#8217;s</em> article on SIDS and bed sharing remained popular for the third month in a row.  Other newly-published papers in the top ten include Vandenplas <em>et al</em>&#8216;s clinical insight on the relationship between asthma and cleaning agents, and Kalil <em>et al&#8217;s </em>review of hospital-acquired pneumonia.</p>\n<table width=\"483\" border=\"1\" cellspacing=\"0\" cellpadding=\"0\">\n<tbody>\n<tr>\n<td valign=\"top\" width=\"50\">Rank</td>\n<td valign=\"top\" width=\"132\">Author(s)</td>\n<td valign=\"top\" width=\"301\">Title</td>\n</tr>\n<tr>\n<td width=\"50\">1</td>\n<td width=\"132\">Werb <em>et al</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://www.bmjopen.bmj.com/content/3/9/e003077.full\" target=\"_blank\">The temporal relationship between drug supply indicators: an audit of international government surveillance systems</a></td>\n</tr>\n<tr>\n<td width=\"50\">2</td>\n<td width=\"132\">Johansson <em>et al</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://www.bmjopen.bmj.com/content/3/9/e003142.full\" target=\"_blank\">Common psychosocial stressors in middle-aged women related to longstanding distress and increased risk of Alzheimer&#8217;s disease: a 38-year longitudinal population study</a></td>\n</tr>\n<tr>\n<td width=\"50\">3</td>\n<td width=\"132\">Carpenter <em>et al</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/3/5/e002299.full\" target=\"_blank\">Bed sharing when parents do not smoke: is there a risk of SIDS? An individual level analysis of five major case–control studies</a></td>\n</tr>\n<tr>\n<td width=\"50\">4</td>\n<td width=\"132\">Taylor <em>et al</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://www.bmjopen.bmj.com/content/3/10/e003219.full\" target=\"_blank\">Prevalence and incidence rates of autism in the UK: time trend from 2004–2010 in children aged 8 years</a></td>\n</tr>\n<tr>\n<td width=\"50\">5</td>\n<td width=\"132\">Doyle <em>et al</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/3/1/e001570.full\" target=\"_blank\">A systematic review of evidence on the links between patient experience and clinical safety and effectiveness</a></td>\n</tr>\n<tr>\n<td width=\"50\">6</td>\n<td width=\"132\">Vandenplas <em>et al</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/3/9/e003568.abstract\" target=\"_blank\">Asthma related to cleaning agents: a clinical insight</a></td>\n</tr>\n<tr>\n<td width=\"50\">7</td>\n<td width=\"132\">Nybergh <em>et al</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/3/5/e002053.full\" target=\"_blank\">Psychometric properties of the WHO Violence Against Women instrument in a male population-based sample in Sweden</a></td>\n</tr>\n<tr>\n<td width=\"50\">8</td>\n<td width=\"132\">Ford <em>et al</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/3/3/e002269.full\" target=\"_blank\">Current treatments in diabetic macular oedema: systematic review and meta-analysis</a></td>\n</tr>\n<tr>\n<td width=\"50\">9</td>\n<td width=\"132\">Kalil <em>et al</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/3/10/e003912.full\" target=\"_blank\">Treatment of hospital-acquired pneumonia with linezolid or vancomycin: a systematic review and meta-analysis</a></td>\n</tr>\n<tr>\n<td width=\"50\">10</td>\n<td width=\"132\">MacCallum <em>et al</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/3/9/e003343.full\" target=\"_blank\">Patient safety and estimation of renal function in patients prescribed new oral anticoagulants for stroke prevention in atrial fibrillation: a cross-sectional study</a></td>\n</tr>\n</tbody>\n</table>\n<p>&nbsp;</p>\n<p>Most read figures are based on pdf downloads and full text views. Abstract views are excluded.<!--TrendMD v2.4.3--></p>\n"},"site":{"siteMetadata":{"title":"Gatsby Default Starter"}}},"pathContext":{"id":"540e253c-d81d-55d3-a01f-265b6416b2e2"}}

